.smoothies {
   padding: 90px 30px 125px;

   &__wrap {
      @extend %flexCenter;
      flex-direction: column;
   }

   &__title {
      margin: 0 0 30px;
      text-align: center;
   }

   &__text {
      max-width: 910px;
      text-align: center;
      margin: 0 0 65px;
   }

   &__grid {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      background: #9ca2a2;
      gap: 2px;
   }

   .grid {

      &__item {
         @extend %flexCenter;
         flex-direction: column;
         justify-content: flex-end;
         margin: 0;
         background: white;
         padding: 15px 35px 35px;
      }
   
      &__img {
         margin-bottom: 20px;
      }
   
      &__caption {
         font-family: Mulish;
         font-size: calc(15px + 10 * (100vw / 1980));
         line-height: 1.6;
         font-weight: 800;
         color: #f2612d;
         letter-spacing: -0.02em;
      }
   }
}