  .footer__header-wrap {
    flex-direction: column;
    align-items: center;
    padding: 30px 0px 0px; }
  .footer__logo {
    margin: 0 auto 30px; }
  .footer_central-block {
    flex-direction: column;
    align-items: flex-start;
    max-width: 310px;
    margin: 0; }
  .footer__information {
    margin: 0 0 30px; }
  .footer__payment {
    margin: 0 0 20px; }
  .footer__list-payment {
    max-width: 330px; }
  .footer__form-input {
    width: 310px;
    height: 50px;
    border-radius: 25px;
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px;
    -ms-border-radius: 25px;
    -o-border-radius: 25px; }
  .footer__form-btn {
    padding-right: 70px; }
  .footer__form::before {
    top: 15px; }
  .footer__form::after {
    top: 60px;
    left: 15px; }
  .footer__social {
    padding-right: 0px;
    transform: translate(-10px, -35px);
    -webkit-transform: translate(-10px, -35px);
    -moz-transform: translate(-10px, -35px);
    -ms-transform: translate(-10px, -35px);
    -o-transform: translate(-10px, -35px); }
  .footer__form-social {
    margin-bottom: 25px; }
  .footer__go-top {
    bottom: 20px;
    right: 20px; } }

@media screen and (max-width: 400px) {
  .about__title--green::after {
    display: none; } }

@media screen and (max-width: 360px) {
  .header__burger-btn {
    top: 20px;
    right: 20px; }
  .header__logo {
    top: 100px; }
  .products__item {
    padding: 70px 0 85px;
    min-width: 300px; }
  .products__img {
    width: 33%; }
  .story__container {
    padding: 10px 15px 80px; }
  .story__title {
    margin: 0 0 20px; }
  .story__content {
    padding-top: 40px;
    margin: 0; }
  .story__img-wrap {
    margin: 60px 0 0; }
  .story__img-wrap::after {
    display: none;
    top: 0;
    left: 0;
    width: 29px;
    height: 21px; }
  .story__btn {
    margin: 0 auto; }
  .blog__container {
    padding: 50px 15px; }
  .blog__content-wrap {
    justify-content: flex-start; }
  .blog__content-wrap::before {
    display: none; }
  .blog__title {
    margin: 0 auto 30px 0; }
  .blog__wrap-img {
    max-width: 738px;
    margin: 0; }
  .blog__right-block {
    padding: 35px 0; }
  .blog__right-block-header {
    margin-bottom: 25px; }
  .blog__text {
    max-width: 300px; }
  .instagram__item {
    min-width: 265px; }
  .instagram__item::after {
    bottom: 10px;
    right: 10px; } }
